import React, { Component } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { Dropdown } from 'primereact/dropdown';
import { connect } from 'react-redux';
import classes from '../styles/Style.module.css';
import { t } from '../../../i18n';
import ResetPassword from './ResetPassword';
import { login, toggleResetPassword } from '../../../redux/login/login.actions';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';

const renderValidationSchema = index => {
  const phoneLength = index
    ? options.filter(option => option.value === index)[0].phoneLength
    : 8;
  const phoneRegExp = new RegExp(`^\\d{${phoneLength}}$`);
  return Yup.object().shape({
    index: Yup.string().required(t('Login:indexRequired')),
    phone: Yup.string()
      .required(t('Login:phoneNumberRequired'))
      .matches(phoneRegExp, t('Login:phoneError', { phoneLength })),
    password: Yup.string()
      .min(8, t('Login:passwordMinCharacters'))
      .max(12, t('Login:passwordMaxCharacters'))
      .required(t('Login:passwordRequired')),
  });
};

const options = [
  { value: '+221', label: '+221', country: 'senegal', phoneLength: 9 },
  { value: '+223', label: '+223', country: 'mali', phoneLength: 8 },
  { value: '+224', label: '+224', country: 'guinea', phoneLength: 8 },
  { value: '+225', label: '+225', country: 'ivory-coast', phoneLength: 8 },
  { value: '+226', label: '+226', country: 'burkina-faso', phoneLength: 8 },
  { value: '+227', label: '+227', country: 'niger', phoneLength: 8 },
  { value: '+228', label: '+228', country: 'togo', phoneLength: 8 },
  { value: '+229', label: '+229', country: 'benin', phoneLength: 8 },
];
const inputClasses = `form-control ${classes.Invalid}`;
const initialVal = {
  device: '3A9687598ADB71A8',
};

class LoginForm extends Component {
  state = {
    storedUser: false,
    showPassword: false,
    chosenIndex: undefined,
  };
  _showPassword = () => {
    this.setState({
      showPassword: true,
    });
  };
  _hidePassword = () => {
    this.setState({ showPassword: false });
  };
  componentDidMount() {
    document.addEventListener('mouseup', this._hidePassword);
    const user = JSON.parse(localStorage.getItem('storedUser'));
    if (user !== null) {
      initialVal.index = user.index;
      initialVal.phone = user.phone;
      initialVal.remember = user.remember;
      this.setState({ storedUser: true });
    }
  }
  componentWillUnmount() {
    document.removeEventListener('mouseup', this._hidePassword);
  }
  render() {
    return (
      <div className="panel-body">
        <Formik
          initialValues={initialVal}
          validationSchema={renderValidationSchema(this.state.chosenIndex)}
          onSubmit={(values, { setSubmitting }) => {
            const user = {
              index: values.index,
              phone: values.phone,
              remember: values.remember,
            };
            sessionStorage.setItem('SessionUserIndex', user.index);
            sessionStorage.setItem('SessionUserPhone', user.phone);
            if (values.remember) {
              localStorage.setItem('storedUser', JSON.stringify(user));
            } else {
              localStorage.removeItem('storedUser');
            }
            setSubmitting(true);
            this.props.onLogin(
              values.index + values.phone,
              values.password,
              values.device,
            );
          }}
        >
          {({
            values,
            errors,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            setTouched,
          }) => {
            const phoneClassName =
              errors.phone || errors.index ? inputClasses : 'form-control';
            const template = option => {
              if (!option.value) return <span>{option.label}</span>;
              else {
                return (
                  <>
                    <img
                      alt={option.country}
                      src={require(`../../../assets/img/countries/${option.country}.svg`)}
                      width="24"
                    />
                    <span>{option.label}</span>
                  </>
                );
              }
            };

            return (
              <form onSubmit={handleSubmit}>
                <div className="form-tel-txt">
                  <div className="form-group select-telephone">
                    <Dropdown
                      name="index"
                      options={options}
                      value={this.state.chosenIndex || initialVal.index}
                      onChange={e => {
                        setFieldValue('index', e.value);
                        this.setState({ chosenIndex: e.value });
                      }}
                      onMouseDown={() => setTouched({ index: true })}
                      placeholder={'+XYZ'}
                      itemTemplate={template}
                    />
                  </div>
                  <div className="form-group">
                    <input
                      className={phoneClassName}
                      name="phone"
                      type="text"
                      placeholder={t('Login:phone')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.phone}
                    />
                    <ExpiringFormFieldError name="index" />
                    <ExpiringFormFieldError name="phone" />
                  </div>
                </div>
                <div className="form-group">
                  <input
                    className={errors.password ? inputClasses : 'form-control'}
                    name="password"
                    type={this.state.showPassword ? 'text' : 'password'}
                    placeholder={t('Login:password')}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                  />
                  <span className="input-show-btn">
                    <button
                      className="btn reveal"
                      type="button"
                      onMouseDown={this._showPassword}
                    />
                  </span>
                  <ExpiringFormFieldError name="password" />
                </div>
                <div className="form-group row login-tools">
                  <div className="col-md-6 login-remember bcp-checkbox">
                    <input
                      name="remember"
                      id="remember"
                      type="checkbox"
                      onChange={handleChange}
                      value={values.checked}
                      checked={values.remember}
                    />
                    <label htmlFor="remember">{t('Login:rememberMe')}</label>
                  </div>
                  <div className="col-md-6 login-forgot-password">
                    <p
                      className="md-trigger popin_btn"
                      data-modal="colored-primary"
                      onClick={this.props.showResetPasswordModal}
                    >
                      {t('Login:forgotPassword')}
                    </p>
                  </div>
                </div>
                <div className="form-group login-submit">
                  <button className="btn btn-primary btn-x1" type="submit">
                    {t('Login:connect')}
                  </button>
                </div>
              </form>
            );
          }}
        </Formik>
        <ResetPassword />
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  onLogin: (username, password, device) => {
    dispatch(login({ username, password, device }));
  },
  showResetPasswordModal: () => {
    dispatch(toggleResetPassword());
  },
});

const mapStateToProps = state => ({
  isLoading: state.Login.isLoading,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(LoginForm);
